.Tiles {
    width: 480px;
    margin: 10px auto;
}

.Controls {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding: 10px;
}

.Controls__round {
    height: 2em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 100%;
    -ms-flex-align: center;
        align-items: center;
    margin-bottom: 1em;
}

.Controls-button {
    font-size: inherit;
    background-color: white;
    border: 1px solid rgba(0, 0, 0, .2);
    border-radius: 3px;
}

.Controls-button:hover {
    border-color: rgba(0, 0, 0, .3);
}

.Controls-button:active {
    background-color: #ffec9b;
}

.Controls__easy {
