    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: baseline;
        align-items: baseline;
}

.Controls__checkbox {
    margin-right: 1em;
}

@media screen and (max-width: 480px) {
    .Tiles {
        width: 100vw;
        margin: 0;
    }

    .Tiles-board {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        max-width: 100%;
    }

    .Controls__checkbox {
        width: 1.5em;
        height: 1.5em;
    }
}



